import { bindings, hook } from '@vl/redata';
import _ from 'lodash';

const bindData = bindings({
  advisorStyle: {
    rules: [
      [
        'data',
        {
          data: {
            advisorStyle: hook((ctx) => {
              const advisor = ctx.get('advisorInfo');
              const appraisalType = _.get(advisor, 'profile.appraisalType') || {};
              return {
                sections: [
                  {
                    title: ctx.apply('i18n.t', 'ProfileAdvisor.styleSlider.title'),
                    textLeft: ctx.apply('i18n.t', 'ProfileAdvisor.styleSlider.friendly'),
                    textRight: ctx.apply('i18n.t', 'ProfileAdvisor.styleSlider.polite'),
                    value: _.get(appraisalType, 'howToSpeak') || 0
                  },
                  {
                    title: ctx.apply('i18n.t', 'ProfileAdvisor.speakSlider.title'),
                    textLeft: ctx.apply('i18n.t', 'ProfileAdvisor.speakSlider.slow'),
                    textRight: ctx.apply('i18n.t', 'ProfileAdvisor.speakSlider.fast'),
                    value: _.get(appraisalType, 'speakingSpeed') || 0
                  },
                  {
                    title: ctx.apply('i18n.t', 'ProfileAdvisor.teachSlider.title'),
                    textLeft: ctx.apply('i18n.t', 'ProfileAdvisor.teachSlider.flexible'),
                    textRight: ctx.apply('i18n.t', 'ProfileAdvisor.teachSlider.direct'),
                    value: _.get(appraisalType, 'howToConveyTheResult') || 0
                  }
                ]
              };
            })
          }
        }
      ]
    ]
  }
});

export default bindData;
