import { bindings, hook } from '@vl/redata';
import _ from 'lodash';

const bindData = bindings({
  advisorSumary: {
    rules: [
      [
        'data',
        {
          data: {
            advisorSumary: hook((ctx) => {
              const advisor = ctx.get('advisorInfo');
              const avatarUrl = _.get(advisor, 'profile.avatarUrl') || '';
              const displayName = _.get(advisor, 'profile.displayName') || '';
              const ratingText = _.get(advisor, 'rating.ratingText');
              const ratingScore = _.get(advisor, 'rating.ratingScore');
              const status = _.get(advisor, 'profile.status');
              const introText = _.get(advisor, 'profile.introText');
              const item = {
                avatarUrl,
                displayName,
                ratingText,
                ratingScore,
                status,
                introText,
              };
              return item;
            }),
            statusName: hook((ctx) => {
              const statusData = ctx.get('advisorSumary.status');
              if (statusData.isOnline && statusData.isReady) {
                return ctx.apply('i18n.t', 'Advisor.online');
              }
              if (statusData.isOnline && !statusData.isReady) {
                return ctx.apply('i18n.t', 'Advisor.busy');
              }
              return ctx.apply('i18n.t', 'Advisor.offline');
            }),
          },
        },
      ],
    ],
  },
});

export default bindData;
