import React from 'react';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import gstyles from '@vl/gstyles';

const View8 = () => {
  return (
    <DIV className="advisorWorkingTime">
      <section id="sectionTime" className="flex flex-col py-4 min-lg:px-6">
        <label className="mb-3 text-base font-semibold">{ctx.apply('i18n.t', 'ProfileAdvisor.workingTime')}</label>
        {ctx.get('advisorWorkingTime.calendar').map((cal, i) => (
          <div className="flex mb-2" key={`day_${i}`}>
            <div className="flex flex-col w-1/3">
              <span className="font-semibold">{cal.day}</span>
              {/* <span className="text-xs text-subColor">(hm nay)</span> */}
            </div>
            <div className="flex-grow-1">
              <div
                className={`grid grid-flow-col grid-cols-2 grid-rows-${
                  cal.data.length <= 2 ? '1' : Math.round((cal.data.length + 1) / 2)
                }`}
              >
                {cal.data.map((time, i) => (
                  <div className="flex" key={`time_${i}`}>
                    {gstyles.icons({ name: 'clock', className: 'mr-3' })}
                    <span className>{time.text}</span>
                  </div>
                ))}
              </div>
            </div>
          </div>
        ))}
        {ctx.get('advisorWorkingTime.hasLoadMore') && (
          <label
            className="flex flex-row items-center justify-center mt-4 mb-0 text-brandb"
            onClick={
              ctx.get('advisorWorkingTime.show')
                ? ctx.get('advisorWorkingTime.onHide')
                : ctx.get('advisorWorkingTime.onShow')
            }
          >
            <span className="mr-1">
              {ctx.get('advisorWorkingTime.show')
                ? ctx.apply('i18n.t', 'Advisor.less')
                : ctx.apply('i18n.t', 'Advisor.more')}
            </span>
            {ctx.get('advisorWorkingTime.show')
              ? gstyles.icons({ name: 'arrowUp', size: 12, fill: gstyles.colors.brandb500 })
              : gstyles.icons({ name: 'arrowDown', size: 12, fill: gstyles.colors.brandb500 })}
          </label>
        )}
      </section>
    </DIV>
  );
};

export default View8;
