import React from 'react';
import { bindings, hook } from '@vl/redata';
import _ from 'lodash';

const bindData = bindings({
  advisorIntroduce: {
    rules: [
      [
        'data',
        {
          data: {
            refIntroduce: hook(() => {
              return {
                detail: React.useRef(),
              };
            }),
            advisorIntroduce: hook((ctx) => {
              const [show, setShow] = React.useState(false);
              const advisor = ctx.get('advisorInfo');
              const detailHtml = _.get(advisor, 'profile.detailHtml') || '';
              const countLines = (element) => {
                if (!element) return 0;
                let divHeight = _.get(element, 'offsetHeight', 0);
                let lineHeight = _.get(element, parseInt('style.lineHeight'), 20);
                let lines = divHeight / lineHeight;
                return lines;
              };
              const checkLoadMore = (elements) => {
                if (!elements) return false;
                let count = 0;
                for (let i = 0, len = elements.length; i < len; i++) {
                  count += countLines(elements[i]);
                  if (count > 12) {
                    return true;
                  }
                }
                return false;
              };

              return {
                hasLoadMore: () => {
                  const detail = ctx.get('refIntroduce.detail');
                  const elements = _.get(detail, 'current.children.0.children');
                  return detailHtml && elements && checkLoadMore(elements);
                },
                detailHtml,
                show,
                onShow: () => {
                  setShow(true);
                },
                onHide: () => {
                  setShow(false);
                },
              };
            }),
          },
        },
      ],
    ],
  },
});

export default bindData;
