import { bindings, hook } from '@vl/redata';
import _ from 'lodash';

const bindData = bindings({
  advisorCategory: {
    rules: [
      [
        'data',
        {
          data: {
            advisorCategory: hook((ctx) => {
              const advisor = ctx.get('advisorInfo');
              const categories = _.get(advisor, 'profile.categories') || [];
              return {
                categories
              };
            })
          }
        }
      ]
    ]
  }
});

export default bindData;
