import React from 'react';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import ReactPlayer from 'react-player/lazy';

const View8 = () => {
  return (
    <DIV className="advisorVideos">
      {!!ctx.get('advisorVideos.videos.length') && (
        <>
          <div className="border-t border-divider" />
          <section id="sectionVideo" className="flex flex-col py-4 min-lg:px-6">
            <label className="mb-3 text-base font-semibold">{ctx.apply('i18n.t', 'ProfileAdvisor.video')}</label>
            <div className="flex flex-row flex-wrap justify-between">
              {ctx.get('advisorVideos.videos').map(({ id, src }) => (
                <div className="mb-2" key={`${id}`}>
                  <ReactPlayer className="overflow-hidden rounded-md" width={170} height={96} url={src} />
                </div>
              ))}
            </div>
          </section>
        </>
      )}
    </DIV>
  );
};

export default View8;
