import React from 'react';
import { bindings, hook } from '@vl/redata';
import _ from 'lodash';
import useRoute from '@vl/hooks/useGbRoute';

const bindData = bindings({
  advisorMenus: {
    rules: [
      [
        'data',
        {
          data: {
            advisorMenus: hook((ctx) => {
              const route = useRoute();
              const hash = _.get(route, 'location.hash', '');
              const [activeMenu, $activeMenu] = React.useState(hash);
              const menuData = [
                {
                  href: '#sectionIntroduce',
                  id: 'menuIntroduce',
                  text: ctx.apply('i18n.t', 'Advisor.aboutMe'),
                  icon: 'personal',
                  show: true,
                },
                {
                  href: '#sectionCategory',
                  id: 'menuCategory',
                  text: ctx.apply('i18n.t', 'ProfileAdvisor.category'),
                  icon: 'category',
                  show: true,
                },
                {
                  href: '#sectionPhoto',
                  id: 'menuPhoto',
                  text: ctx.apply('i18n.t', 'ProfileAdvisor.photo'),
                  icon: 'photo-library',
                  show: true,
                },
                {
                  href: '#sectionVideo',
                  id: 'menuVideo',
                  text: ctx.apply('i18n.t', 'ProfileAdvisor.video'),
                  icon: 'video-library',
                  show: true,
                },
                {
                  href: '#sectionStep',
                  id: 'menuStep',
                  text: ctx.apply('i18n.t', 'ProfileAdvisor.usingSteps'),
                  icon: 'flow-right',
                  show: true,
                },
                {
                  href: '#sectionStyle',
                  id: 'menuStyle',
                  text: ctx.apply('i18n.t', 'ProfileAdvisor.consultingStyle'),
                  icon: 'chart',
                  show: true,
                },
                {
                  href: '#sectionTime',
                  id: 'menuTime',
                  text: ctx.apply('i18n.t', 'ProfileAdvisor.workingTime'),
                  icon: 'clock-filled',
                  show: true,
                },
                {
                  href: '#sectionPackage',
                  id: 'menuPackage',
                  text: ctx.apply('i18n.t', 'ProfileAdvisor.callPackages'),
                  icon: 'price-package',
                  show: true,
                },
                {
                  href: '#sectionReview',
                  id: 'menuReview',
                  text: ctx.apply('i18n.t', 'ProfileAdvisor.commentAndReview'),
                  icon: 'star2',
                  show: !!ctx.get('paginationModel.items.length'),
                },
              ];
              const triggerClickMenu = (menuId) => {
                if (!menuId) return;
                const element = document.querySelector(menuId);
                if (element) {
                  element.click();
                }
              };
              React.useEffect(() => {
                if (hash) {
                  const menu = hash.replace('section', 'menu');
                  triggerClickMenu(menu);
                }
              }, []);
              return {
                data: menuData,
                activeMenu,
                onSelectMenu: (item) => {
                  const id = _.get(item, 'id', 'sectionIntroduce');
                  // ctx.apply('@refs.scrollToRef', ctx.get(`@refs.${id}`));
                  $activeMenu(id);
                },
              };
            }),
          },
        },
      ],
    ],
  },
});

export default bindData;
