import React from 'react';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import {
  Rate, Divider, Row, Col
} from 'antd';
import gstyles from '@vl/gstyles';
import { Avatar } from '@uz/unitz-components-web/Avatar';
import { ResponsiveProps } from '@uz/unitz-components-web/Responsive';
import _ from 'lodash';

const View8 = () => {
  return (
    <DIV className="advisorReviews">
      <section id="sectionReview" className="flex flex-col py-4 min-lg:px-6">
        {!!ctx.get('paginationModel.items.length') && (
          <>
            <div className="flex mb-4 max-lg:justify-between min-lg:flex-col">
              <label className="mb-2 font-semibold max-lg:text-normal min-lg:text-base">{`${ctx.get(
                '@item.total'
              )} ${ctx.apply('i18n.t', 'ProfileAdvisor.reviews')}`}</label>
              <div className="flex flex-row items-center mb-4 max-lg:-mt-2 max-lg:-mr-4">
                <div className="mr-2 -mb-1 text-base">{ctx.get('@item.ratingText')}</div>
                <ResponsiveProps sm={{ scale: 90, translateX: 2 }} lg={{ scale: 100, translateX: 0 }}>
                  {(resProps) => (
                    <Rate
                      className={` transform 
                        scale-${_.get(resProps, 'scale', 100)}
                        -translate-x-${_.get(resProps, 'translateX', 0)}
                      `}
                      disabled
                      count={5}
                      value={ctx.get('@item.ratingScore')}
                    />
                  )}
                </ResponsiveProps>
              </div>
            </div>
            {ctx.get('@item.reviews').map((review) => (
              <React.Fragment key={_.get(review, 'id')}>
                <div className="flex">
                  <div className="w-12 mr-3">
                    <Avatar size={48} src={_.get(review, 'reviewer.avatarUrl')} />
                  </div>
                  <div className="flex flex-col flex-1">
                    <div className="flex justify-between">
                      <div className="flex flex-row">
                        <span className="mr-3 text-sm font-semibold text-main">
                          {_.get(review, 'reviewer.displayName')}
                        </span>
                        {gstyles.icons({ name: 'videoAdvisor', size: 18, fill: gstyles.colors.brandb500 })}
                      </div>
                      <div className="text-black300">{_.get(review, 'reviewer.seenAtText')}</div>
                    </div>
                    <ResponsiveProps sm={{ scale: 90, translateX: 5 }} lg={{ scale: 100, translateX: 0 }}>
                      {(resProps) => (
                        <Rate
                          className={`transform 
                            scale-${_.get(resProps, 'scale', 100)}
                            -translate-x-${_.get(resProps, 'translateX', 0)}
                            `}
                          disabled
                          count={5}
                          value={_.get(review, 'rating.ratingScore')}
                        />
                      )}
                    </ResponsiveProps>
                    {!!_.get(review, 'reviewText') && (
                      <p className="mt-3 mb-0 text-sm font-normal text-main">{_.get(review, 'reviewText')}</p>
                    )}
                  </div>
                </div>
                <Divider />
              </React.Fragment>
            ))}
            <Row justify="space-between">
              <Col span={6}>
                {/* eslint-disable-next-line */}
                <label
                  className="m-0 cursor-pointer w-22 text-brandb"
                  onClick={ctx.get('paginationModel.onPreviousPage')}
                >
                  {ctx.get('paginationModel.hasPreviousPage') && ctx.apply('i18n.t', 'ProfileAdvisor.prevPage')}
                </label>
              </Col>
              <span>{`${ctx.apply('i18n.t', 'ProfileAdvisor.page')} ${ctx.get('paginationModel.page') + 1}`}</span>
              <Col span={6}>
                {/* eslint-disable-next-line */}
                <label
                  className="w-full m-0 text-right cursor-pointer text-brandb"
                  onClick={ctx.get('paginationModel.onNextPage')}
                >
                  {ctx.get('paginationModel.hasNextPage') && ctx.apply('i18n.t', 'ProfileAdvisor.nextPage')}
                </label>
              </Col>
            </Row>
          </>
        )}
      </section>
    </DIV>
  );
};

export default View8;
