import React from 'react';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import cx from 'classnames';
import gstyles from '@vl/gstyles';

const View8 = () => {
  return (
    <DIV className="advisorIntroduce">
      <section id="sectionIntroduce" className="flex flex-col py-4 min-lg:px-3">
        <label className="text-base font-semibold">{ctx.apply('i18n.t', 'Advisor.aboutMe')}</label>
        <p
          className={cx('text-sm font-normal', { 'text-ellipsis-12': !ctx.get('advisorIntroduce.show') })}
          ref={ctx.get('refIntroduce.detail')}
        >
          {ctx.get('advisorIntroduce.detailHtml')}
        </p>

        {!!ctx.apply('advisorIntroduce.hasLoadMore') && (
          <label
            className="flex flex-row items-center justify-center cursor-pointer text-brandb"
            onClick={
              ctx.get('advisorIntroduce.show') ? ctx.get('advisorIntroduce.onHide') : ctx.get('advisorIntroduce.onShow')
            }
          >
            <span className="mr-2">
              {ctx.get('advisorIntroduce.show')
                ? ctx.apply('i18n.t', 'Advisor.less')
                : ctx.apply('i18n.t', 'Advisor.more')}
            </span>
            {ctx.get('advisorIntroduce.show')
              ? gstyles.icons({ name: 'arrowUp', size: 12, fill: gstyles.colors.brandb500 })
              : gstyles.icons({ name: 'arrowDown', size: 12, fill: gstyles.colors.brandb500 })}
          </label>
        )}
      </section>
    </DIV>
  );
};

export default View8;
