import React from 'react';
import { bindings, hook } from '@vl/redata';
import _ from 'lodash';

const bindData = bindings({
  advisorWorkingTime: {
    rules: [
      [
        'data',
        {
          data: {
            advisorWorkingTime: hook((ctx) => {
              const [show, setShow] = React.useState(false);
              const advisor = ctx.get('advisorInfo');
              const calendar = _.get(advisor, 'profile.calendar', []);
              return {
                calendar: show ? calendar : calendar.slice(0, 1),
                hasLoadMore: calendar.length > 1,
                show,
                onShow: () => {
                  setShow(true);
                },
                onHide: () => {
                  setShow(false);
                }
              };
            })
          }
        }
      ]
    ]
  }
});

export default bindData;
