import { bindings, hook } from '@vl/redata';

const bindData = bindings({
  advisorVideos: {
    rules: [
      [
        'data',
        {
          data: {
            advisorVideos: hook((ctx) => {
              const videos = ctx.get('advisorService.videos', []);
              return {
                videos,
              };
            }),
          },
        },
      ],
    ],
  },
});

export default bindData;
