import React from 'react';
import { bindings, hook } from '@vl/redata';
import useLoadingModel from '@vl/hooks/useLoadingModel';
import AdvisorProfileFormatter from '@uz/unitz-models/AdvisorProfileModel/formatter';
import PaginationModel from '@uz/unitz-models/PaginationModel';

import AdvisorModel from '@uz/unitz-models/AdvisorModel';
import _ from 'lodash';
import usePromiseSource from '@vl/hooks/usePromiseSource';

import useRoute from '@vl/hooks/useGbRoute';

const bindData = bindings({
  pageContainer: {
    rules: [
      [
        'data',
        {
          data: {
            loadingModel: hook(useLoadingModel(() => ({ fetchProfile: true }))),
            advisorItem: hook(() => {
              const route = useRoute();
              const routeParams = _.get(route, 'params', {});
              return routeParams;
            }),
            advisorService: hook((ctx) => {
              const route = useRoute();
              const routeParams = _.get(route, 'params', {});
              const advisorId = _.get(routeParams, 'id', '');
              const advisorService = usePromiseSource(
                async () => {
                  if (!advisorId) return null;
                  try {
                    const advisorProfile = await AdvisorModel.find(
                      `where: {id: { _eq: "${advisorId}" }}`,
                      `
                      services(order_by: {price: {price_amount: asc}}) {
                        price { per_unit per_amount price_currency price_amount }
                        id
                        service { id name kind }
                      }
                      reviews {
                        id
                        review_text
                        score
                        updated_at
                        author {
                          profile { avatar_url display_name id }
                        }
                      }
                      review_agg { avg_score }
                      reviews_aggregate {
                        aggregate { count }
                      }
                      presence{
                        status
                      }
                      readiness{
                        is_ready
                        on_transaction_id
                        is_accept_service
                      }
                      service_kind { id is_active kind }
                      profile {
                        display_name
                        avatar_url
                        ref_ctf_eid
                        detail_html
                        intro_text
                        categories {
                          category { id display_name avatar_url icon }
                        }
                        calendar
                        appraisal_type
                      }
                      photos {
                        id
                        src
                      }
                    `
                    );
                    return advisorProfile.toObject();
                  } catch (err) {}
                },
                null,
                [advisorId]
              );

              const profile = _.get(advisorService, '0.profile');
              const statusData = _.pick(advisorService, ['0.presence', '0.readiness']);
              const services = _.get(advisorService, '0.services', []);
              const item = _.get(advisorService, '0', []);
              const reviews = _.get(advisorService, '0.reviews', []);
              const reviewAggregate = _.get(advisorService, '0.reviews_aggregate.aggregate');
              const result = {
                id: advisorId,
                profile: {
                  id: AdvisorProfileFormatter.profileId(ctx)(profile),
                  displayName: AdvisorProfileFormatter.displayName(ctx)(profile),
                  avatarUrl: AdvisorProfileFormatter.avatarUrl(ctx)(profile),
                  categories: AdvisorProfileFormatter.categories(ctx)(profile),
                  detailHtml: AdvisorProfileFormatter.renderHTML(ctx)(profile),
                  introText: AdvisorProfileFormatter.introText(ctx)(profile),
                  appraisalType: AdvisorProfileFormatter.appraisalType(ctx)(profile),
                  calendar: AdvisorProfileFormatter.calendar(ctx)(profile),
                  status: AdvisorProfileFormatter.status(ctx)(statusData),
                },
                services: AdvisorProfileFormatter.services(ctx)(services),
                rating: {
                  ratingText: AdvisorProfileFormatter.reviewText(ctx)(item),
                  ratingScore: AdvisorProfileFormatter.reviewScore(ctx)(item),
                },
                reviews: AdvisorProfileFormatter.reviews(ctx)(reviews),
                reviewCount: AdvisorProfileFormatter.reviewCount(ctx)(reviewAggregate),
                photos: _.get(advisorService, '0.photos', []),
                videos: _.get(advisorService, '0.videos', []),
                consultingSteps: [],
                consultingStyle: [],
              };
              return result;
            }),
            paginationModel: hook((ctx) => {
              const route = useRoute();
              const routeParams = _.get(route, 'params', {});
              const advisorId = _.get(routeParams, 'id', '');
              const pagingModel = React.useMemo(() => {
                const pagingModel = PaginationModel.fromConfig({
                  Model: AdvisorModel,
                  dataQuery: `
                    advisor_review(where: {target_id: {_eq: "${advisorId}"}}) {
                      id
                      review_text
                      score
                      author {
                        profile {
                          id
                          display_name
                          avatar_url
                        }
                      }
                      updated_at
                      created_at
                    }
                  `,
                  aggQuery: `
                    advisor_review_aggregate(where: {target_id: {_eq: "${advisorId}"}}) {
                      aggregate {
                        count
                      }
                    }
                  `,
                  pageSize: 8,
                });
                return pagingModel;
              }, []);

              pagingModel.hookState();

              React.useEffect(() => {
                pagingModel.onLoadPageHandler(0);
              }, [pagingModel]);

              const pagination = {
                items: pagingModel.useState('items'),
                page: pagingModel.useState('page'),
                total: pagingModel.useState('total'),
                isLoading: pagingModel.useState('isLoading'),
                hasPreviousPage: pagingModel.useState('hasPreviousPage'),
                hasNextPage: pagingModel.useState('hasNextPage'),
                onRefresh: () => pagingModel.onRefresh(),
                onPreviousPage: () => pagingModel.onPreviousPageHandler(),
                onNextPage: () => pagingModel.onNextPageHandler(),
                onLoadPage: (page) => pagingModel.onLoadPageHandler(page),
              };
              return pagination;
            }),
          },
        },
      ],
    ],
  },
  isLoading: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.apply('loadingModel.isLoading')),
        },
      ],
    ],
  },
  notLoading: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => !ctx.apply('loadingModel.isLoading')),
        },
      ],
    ],
  },
});

export default bindData;
