import { bindings, hook } from '@vl/redata';
import _ from 'lodash';

const bindData = bindings({
  advisorActionCard: {
    rules: [
      [
        'data',
        {
          data: {
            advisorActionCard: hook((ctx) => {
              const advisor = ctx.get('advisorInfo');
              const video = _.get(advisor, 'services.VideoService');
              const voice = _.get(advisor, 'services.VoiceService');
              const services = _.concat(voice, video);
              const minService = _.min(services, (service) => service.price.per_amount);
              const advisorId = _.get(advisor, 'id') || '';
              return {
                minService,
                advisorId,
              };
            }),
          },
        },
      ],
    ],
  },
});

export default bindData;
