import React from 'react';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import cx from 'classnames';
import gstyles from '@vl/gstyles';
import { Anchor } from 'antd';
import styled from 'styled-components';

const { Link } = Anchor;
const CustomAnchor = styled.div`
  .ant-anchor-wrapper {
    margin: 0;
    padding: 0;
  }
  .ant-anchor-ink {
    display: none;
  }
  .ant-anchor-link {
    padding: 0;
  }
`;
const View8 = () => {
  return (
    <DIV className="advisorMenus">
      <section className="flex flex-col px-6 py-4 w-66">
        <CustomAnchor>
          <Anchor getContainer={() => window} affix={false}>
            {ctx.get('advisorMenus.data').map(
              (menu) => !!menu.show && (
                <Link
                  className="p-0"
                  key={menu.id}
                  href={`${menu.href}`}
                  title={
                    <div
                      id={menu.id}
                      className={cx(
                        'flex items-center text-base font-normal cursor-pointer py-2 px-3 rounded-lg',
                        {
                          'bg-brandb-lightest text-main font-semibold':
                              ctx.get('advisorMenus.activeMenu') === menu.id,
                        },
                        {
                          'text-sub': ctx.get('advisorMenus.activeMenu') !== menu.id,
                        }
                      )}
                      onClick={() => ctx.apply('advisorMenus.onSelectMenu', menu)}
                    >
                      {gstyles.icons({
                        className: 'mr-2',
                        name: menu.icon,
                        size: 20,
                        fill:
                            ctx.get('advisorMenus.activeMenu') === menu.id
                              ? gstyles.colors.brandb500
                              : gstyles.colors.sub,
                      })}
                      <span className="text-ellipsis-4">{menu.text}</span>
                    </div>
                  }
                />
              )
            )}
          </Anchor>
        </CustomAnchor>
      </section>
    </DIV>
  );
};

export default View8;
