import { bindings, hook } from '@vl/redata';

const bindData = bindings({
  advisorPhotos: {
    rules: [
      [
        'data',
        {
          data: {
            advisorPhotos: hook((ctx) => {
              const images = ctx.get('advisorService.photos', []);
              // const images = [1, 2, 3, 4, 5];
              return {
                images,
              };
            }),
          },
        },
      ],
    ],
  },
});

export default bindData;
