import React from 'react';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import { Image } from '@uz/unitz-components-web/Image';

const View8 = () => {
  return (
    <DIV className="advisorPhotos">
      {!!ctx.get('advisorVideos.images.length') && (
        <>
          <div className="border-t border-divider" />
          <section id="sectionPhoto" className="flex flex-col py-4 min-lg:px-6">
            <label className="mb-3 text-base font-semibold">{ctx.apply('i18n.t', 'ProfileAdvisor.photo')}</label>
            <div className="flex flex-row flex-wrap">
              {ctx.get('advisorPhotos.images').map(({ id, src }) => (
                <div className="mb-2 mr-2" key={`${id}`}>
                  <Image
                    className="w-24 h-24 rounded-md"
                    src={src}
                    width={96}
                    height={96}
                    preview={{
                      src: Image.createImageUrl({ src, size: 'cover' }).src,
                    }}
                  />
                </div>
              ))}
            </div>
          </section>
        </>
      )}
    </DIV>
  );
};

export default View8;
