import React from 'react';
import { bindings, hook } from '@vl/redata';
import AdvisorProfileFormatter from '@uz/unitz-models/AdvisorProfileModel/formatter';
import _ from 'lodash';

const bindData = bindings({
  advisorReviews: {
    rules: [
      [
        'data',
        {
          data: {
            '@item': hook((ctx) => {
              const [show, setShow] = React.useState(false);
              const advisor = ctx.get('advisorInfo');
              const total = _.get(advisor, 'reviewCount');
              const ratingText = _.get(advisor, 'rating.ratingText');
              const ratingScore = _.get(advisor, 'rating.ratingScore');
              const item = {
                total,
                ratingText,
                ratingScore,
                show,
                onShow: () => {
                  setShow(true);
                },
                onHide: () => {
                  setShow(false);
                },
                reviews: AdvisorProfileFormatter.reviews(ctx)(ctx.get('paginationModel.items') || []),
              };
              return item;
            }),
          },
        },
      ],
    ],
  },
});

export default bindData;
