import React from 'react';
import { bindings, hook } from '@vl/redata';
import _ from 'lodash';

const bindData = bindings({
  advisorCallPackages: {
    rules: [
      [
        'data',
        {
          data: {
            advisorCallPackages: hook((ctx) => {
              const [tab, setTab] = React.useState('call');
              const advisor = ctx.get('advisorInfo');
              const voicePacks = _.get(advisor, 'services.VoiceService') || [];
              const videoPacks = _.get(advisor, 'services.VideoService') || [];

              return {
                tab,
                packages: tab === 'call' ? voicePacks : videoPacks,
                onSelectTab: (type) => {
                  setTab(type);
                }
              };
            })
          }
        }
      ]
    ]
  }
});

export default bindData;
